<template>
<md-card>
  <md-card-content>
    <div class="md-layout md-gutter">
      <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-for="request in requests" :key="request._id">
        <requestCard :manager="manager" :request="request"/>
      </div>
    </div>
  </md-card-content>
</md-card>
</template>

<script>
import requestCard from './requestCard'
export default {
  props: ['manager'],
  components: {
    requestCard
  },
  data: () => ({
    requests: [
      {_id: '1', name: '', text: '2000'},
      {_id: '2', name: '', text: ''},
      {_id: '3', name: '', text: '3'},
      {_id: '4', name: '', text: '5000'},
      {_id: '5', name: '', text: ''},
      {_id: '6', name: '', text: ''},
      {_id: '7', name: '', text: ''},
      {_id: '8', name: '', text: '3LDK'},
      {_id: '9', name: '', text: '1000'},
      {_id: '10', name: '', text: ''}
    ]
  })
}
</script>

<style scoped>
.md-layout-item {
  margin-bottom: 10px;
}
</style>



