<template>
<md-dialog :md-active.sync="showPublishDialog">
  <md-dialog-title></md-dialog-title>

  <md-dialog-content md-dynamic-height>

    <div class="md-layout md-gutter md-alignment-center">
      <div class="md-layout-item md-size-50 md-small-size-100">
        <md-field>
          <label for="station"></label>
          <md-select v-model="station" name="station" id="station">
            <md-option value="1"></md-option>
            <md-option value="2"></md-option>
            <md-option value="3"></md-option>
            <md-option value="4"></md-option>
            <md-option value="5"></md-option>
            <md-option value="6"></md-option>
            <md-option value="7"></md-option>
          </md-select>
        </md-field>
      </div>
      <div class="md-layout-item md-size-50 md-small-size-100">
        <md-field :class="{'md-invalid': !isMinuteValid}">
          <label></label>
          <md-input v-model="minute" type="number"></md-input>
          <span class="md-error">1</span>
          <span class="md-helper-text"></span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-50 md-small-size-100">
        <md-field>
          <label for="layout"></label>
          <md-select v-model="layout" name="layout" id="layout" class="frontier">
            <md-option value="0">1R</md-option>
            <md-option value="1">1DK</md-option>
            <md-option value="2">2DK</md-option>
            <md-option value="3">2LDK</md-option>
            <md-option value="4">3DK</md-option>
            <md-option value="5">3LDK</md-option>
            <md-option value="6">4DK</md-option>
          </md-select>
        </md-field>
      </div>
      <div class="md-layout-item md-size-50 md-small-size-100">
        <label>{{percent * amount}}</label>
        <input id="amount" type="range" v-model.number="percent">
      </div>
      <div class="md-layout-item md-size-100">
        <md-field>
          <label></label>
          <md-textarea v-model="comment"></md-textarea>
          <span class="md-helper-text"></span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-100">
        <md-field>
          <label></label>
          <md-file v-model="photos" multiple />
        </md-field>
      </div>
    </div>

  </md-dialog-content>

  <md-dialog-actions>
    <md-button class="md-primary" @click="publish()"></md-button>
    <md-button class="md-primary" @click="showPublishDialog = false"></md-button>
  </md-dialog-actions>
</md-dialog>
</template>

<script>
import utils from '@/tool/utils.js'
export default {
  data: () => ({
    showPublishDialog: false,
    photos: null,
    station: null,
    minute: null,
    layout: null,
    percent: 0,
    amount: 500,
    comment: null
  }),
  created () {
    let self = this
    utils.event.$on('SHOW_PUBLISH_DIALOG', () => {
      self.showPublishDialog = true
    })
  },
  computed: {
    isMinuteValid () {
      if (this.minute === null || this.minute === '') return true
      if (parseFloat(this.minute) <= 0) return false
      if (Math.round(parseFloat(this.minute)) === parseFloat(this.minute)) return true
      return false
    }
  },
  methods: {
    publish () {
      this.$router.push({name: 'buy'})
    }
  },
  beforeDestroy () {
    utils.event.$off('SHOW_PUBLISH_DIALOG')
  }
}
</script>

<style lang="scss" scoped>

</style>



