      <md-card-header>
        <div class="md-title"></div>
      </md-card-header>
      <md-card-content>
        <div class="md-layout md-gutter">
          <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-for="request in requests" :key="request._id">
            <requestCard :manager="manager" :request="request"/>
          </div>
        </div>
      </md-card-content>
    </md-card>
  </div>
  <div class="segment">
    <md-card>
      <md-card-header>
        <div class="md-title"></div>
      </md-card-header>
      <md-card-content>
        <div class="md-layout md-gutter">
          <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-for="request in requests" :key="request._id">
            <requestCard :manager="manager" :request="request"/>
          </div>
        </div>
      </md-card-content>
    </md-card>
  </div>
  <div class="segment">
    <md-card>
      <md-card-header>
        <div class="md-title"></div>
      </md-card-header>
      <md-card-content>
        <div class="md-layout md-gutter">
          <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-for="request in requests" :key="request._id">
            <requestCard :manager="manager" :request="request"/>
          </div>
        </div>
      </md-card-content>
    </md-card>
  </div>
  <div class="segment">
    <md-card>
      <md-card-header>
        <div class="md-title"></div>
      </md-card-header>
      <md-card-content>
        <div class="md-layout md-gutter">
          <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-for="request in requests" :key="request._id">
            <requestCard :manager="manager" :request="request"/>
          </div>
        </div>
      </md-card-content>
    </md-card>
  </div>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import contactCard from '../contact/contactCard'
import requestCard from '../estate/sell/requestCard'
export default {
  props: ['manager'],
  components: {
    contactCard,
    requestCard
  },
  data: () => ({
    requests: [
      {_id: '1', name: '', text: '2000'},
      {_id: '2', name: '', text: ''},
      {_id: '3', name: '', text: '3'},
      {_id: '4', name: '', text: '5000'}
    ],
    contacts: [
      {_id: '1', name: '', text: 'ABC'},
      {_id: '2', name: ' ', text: ''},
      {_id: '3', name: '', text: ''},
      {_id: '4', name: '', text: ''}
    ]
  }),
  methods: {
    test () {
      utils.restGet('/api/getData', {something: 'testdata'}).then(
        response => {
          console.log(JSON.stringify(response))
        }
      )
    },
    test2 () {
      utils.restPost('/api/postData', {something: 'test2data'}).then(
        response => {
          console.log(JSON.stringify(response))
        }
      )
    },
    test3 () {
      utils.restPut('/api/putData', {something: 'test3data'}).then(
        response => {
          console.log(JSON.stringify(response))
        }
      )
    },
    test4 () {
      utils.restDelete('/api/deleteData', {something: 'test4data'}).then(
        response => {
          console.log(JSON.stringify(response))
        }
      )
    }
  }
}
</script>

<style scoped>
.segment {
  margin: 10px 0px;
}
.md-layout-item {
  margin-bottom: 10px;
}
</style>



