<template>
<md-empty-state class="md-accent" md-icon="highlight_off" md-label="" md-description="">
  <div class="error-content">
    <router-link to="/login"></router-link>
  </div>
  <div class="">
    
  </div>
</md-empty-state>
</template>

<script>
import manager from '@/store/manager.js'
export default {
  props: ['manager'],
  created () {
    manager.logout()
  }
}
</script>

<style scoped>
.error-content {
  margin: 20px;
}
</style>



